export const validateInput = (data, eventType, stateSetter) => {
  if (data === "") {
      const emptyLoginText = "     , ,  !";
      if (!eventType) {
          stateSetter(emptyLoginText);
          return false
      } else {
          alert(emptyLoginText)
          return false
      }
  }

  if (data.length > 13) {
      const longLoginText = "      13 , ,   " +
              " "
      if (!eventType) {
          stateSetter(longLoginText);
          return false
      } else {
          alert(longLoginText);
          return false
      }
  }

  return true
}